<template lang="pug">
	label.skill
		.name {{skill.name}}
		.percents-value
			input.input(
			type="text",
			:value="skill.percents"
			)
		.percent-type %
		.delete
			button.button-delete(type="button" @click="removeCurSkill(skill.id)")
				| X
</template>
<script>
	import { mapMutations } from 'vuex';
	export default {
		props: {
			skill: Object,
		},
		methods: {
			...mapMutations(['removeSkill']),
			removeCurSkill(skillId) {			 
				this.removeSkill(skillId);			 
			}
		}
	}
</script>
<style lang="scss" scoped>
	.skill {
		width: 100%;
		display: flex;
		margin-bottom: rem(20);
		&:hover {
			cursor: context-menu;
		}
		&:hover .button-delete, &:focus .button-delete{
			opacity: 1;
			cursor: pointer;
		}
	}
	.name {
		width: 40%;
		flex-grow: 1;
	}
	.percents-value {
		width: rem(30);
		text-align: right;
	}
	.input {
		width: rem(30);
		outline: none;
		background: transparent;
		border: none;
		padding-left: rem(3);
		&:focus, &:hover {
			border: 1px solid $brand-color;
			border-radius: 2px;
		}
	}
	.percent-type {
		width: rem(15);
		text-align: center;
	}
	.delete {
		width: 5rem;
		text-align: right;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}
	.button-delete {
		width: rem(25);
		height: rem(25);
		border: none;
		outline: none;
		border-radius: 2px;
		display: flex;
		justify-content: center;
		align-items: center;
		opacity: 0;
		text-align: center;
		transition: opacity 0.2s;
		color: $brand-color;
	}
</style>


